@import "~video.js/dist/video-js.min.css";
.video-js {
  width: 100%;
}
.video-js video {
  width: 100%;
}
.video-koko {
  height: auto;
  outline: none;
}
.video-koko button {
  outline: none;
}
.video-koko video {
  transition: opacity 0.3s ease-in-out;
}
.video-koko .vjs_video_3-dimensions {
  height: auto;
}
.video-koko .vjs-control-bar {
  transition: visibility 0.05s, opacity 0.05s;
  background-image: linear-gradient(to bottom, rgba(17, 17, 17, 0), rgba(17, 17, 17, 0.5));
  background-color: transparent;
}
.video-koko .vjs-control-bar .vjs-remaining-time {
  display: none;
}
.video-koko .vjs-control-bar .vjs-picture-in-picture-control {
  margin-left: auto;
}
.video-koko .vjs-control-bar .vjs-progress-control {
  position: absolute;
  width: 100%;
  top: -3em;
}
.video-koko .vjs-control-bar .vjs-progress-control .vjs-load-progress {
  background: rgba(255, 255, 255, 0.4);
}
.video-koko .vjs-control-bar .vjs-progress-control .vjs-play-progress {
  background-color: #2962ff;
}
.video-koko .vjs-control-bar .vjs-progress-control .vjs-play-progress:before {
  display: none;
}
.video-koko .vjs-poster {
  transition: all 0.3s ease-in-out;
}
.video-koko .vjs-big-play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: none;
  outline: none;
}
.video-koko .vjs-tech {
  outline: none;
  position: relative;
  top: inherit;
  left: inherit;
  width: inherit;
  height: inherit;
}
