.post {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  cursor: pointer;
}
.post.overlay {
  position: relative;
}
.post.overlay .profile {
  color: #FFFFFF;
  width: 100%;
  text-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
  position: absolute;
  bottom: 0;
}
.post.overlay .profile .profile-button button {
  display: none;
}
.post .post-image {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}
.post .post-image:before {
  background-size: cover;
  filter: blur(8px);
  background-repeat: no-repeat;
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  z-index: -2;
}
.post .post-image .post-image-background {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.95);
  width: 100%;
  height: 100%;
  z-index: -1;
}
.post .post-image img {
  max-width: 100%;
  max-height: 100%;
}
.profile {
  padding: 14px 16px;
  display: flex;
}
.profile .profile-image {
  width: 40px;
  height: 40px;
  position: relative;
  flex: none;
}
.profile .profile-image img {
  width: 100%;
  height: 100%;
}
.profile .profile-image > div {
  overflow: hidden;
  height: 100%;
  border-radius: 50%;
}
.profile .profile-image .vendor-image {
  position: absolute;
  width: 16px;
  height: 16px;
  top: -4px;
  right: -4px;
}
.profile .profile-desc {
  font-size: 14px;
  display: flex;
  align-items: flex-start;
  margin-left: 12px;
  flex-direction: column;
  justify-content: space-between;
  flex: 1;
  min-width: 0;
  padding-right: 4px;
}
.profile .profile-desc > div {
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.profile .profile-desc > div:first-child {
  justify-content: normal;
}
.profile .profile-desc .post-date {
  font-size: 12px;
}
.profile .profile-button {
  align-self: center;
  margin-left: auto;
}
.profile .profile-button button {
  width: 24px;
  height: 24px;
  margin-right: 16px;
}
.profile .profile-button button:last-child {
  margin-right: 0;
}
