.lnb {
  display: flex;
  margin: 0 auto;
  align-items: stretch;
}
.lnb li {
  display: inline;
  margin: 0 8px;
}
.lnb li a {
  text-decoration: none;
  font-size: 20px;
  font-weight: bold;
  color: #111111;
  padding: 7px 16px 8px;
  border-radius: 20px;
}
.lnb li a:visited,
.lnb li a:active {
  color: inherit;
}
.lnb li a.active {
  background-color: #536dfe;
  color: #FFFFFF;
}
