.discover-post {
  display: flex;
}
.discover-post-grid {
  display: flex;
  margin-left: -16px;
  /* gutter size offset */
  max-width: 1440px;
  width: 100%;
  padding: 24px 48px;
}
.discover-post-grid .grid-column {
  padding-left: 16px;
  /* gutter size offset */
}
.discover-post-grid .grid-column .post {
  height: auto;
  margin-bottom: 16px;
}
.post-detail {
  background-color: #111111;
  padding: 48px;
  position: sticky;
  width: 50vw;
  height: 100vh;
  top: 0;
}
.post-detail .post {
  background-color: #FFFFFF;
  height: auto;
  cursor: default;
}
.post-detail .post-detail-header {
  margin-bottom: 24px;
  display: flex;
  justify-content: space-between;
}
.post-detail .post-detail-header button {
  width: 24px;
  height: 24px;
}
.post-detail .post-detail-header button img {
  width: 16px;
  height: 16px;
}
.post-detail .post-detail-header .post-detail-arrow {
  display: flex;
}
